import styles from './style.module.scss'
import React, {useState} from "react";
import {Dresscode} from "./Dresscode";
import {Pressents} from "./Pressents";
import {Schedule} from "./Schedule";
import {Address} from "./Address";

export const InformationPart = () => {

    const [showAddress, setShowAddress] = useState(false);
    const [showSchedule, setShowSchedule] = useState(false);
    const [showPressents, setShowPressents] = useState(false);
    const [showDresscode, setShowDresscode] = useState(false);

    return (
        <div className={styles.information} id={'information'}>
            <h2 className={styles.center_content} id={styles.information_title}>Informace</h2>
            <p className={styles.center_content}>
                <p>
                    Zde Vs budeme prbn informovat o vem monm i nemonm.
                </p>
                <ul className={styles.ul_text_align}>
                    <li>
                        Na svatb budeme mt profesionln fotografku a profesionlnho kameramana.
                        Proto Vs moc dme, abyste v dleit momenty odolali a nechali sv mobiln telefony v kapsch a
                        proili tyto jedinen chvle s nmi. Uvidte, e vsledn fotky a videa budou bez telefon v zbrech
                        msto Vaich tv vypadat mnohem lpe. A to plat i pro otce nevsty!!!
                    </li>
                    <li>
                        Pokud byste nm chtli udlat radost hzenm konfet, re nebo jinch vc,
                        berte prosm na vdom, e jsou
                        nam svatebnm mstem ZAKZNY. Na novomanelsk palr budeme mt pro Vs v
                        omezenm mnostv k dispozici bublifuky
                        a suen lstky. Pokud byste nco ze zmnnho mli doma navc, mete si je
                        pinst.
                    </li>
                    <li>
                        Pokud mte njak alergie, intolerance nebo jin omezen v jdle, dejte nm prosm vdt co
                        nejdve.
                    </li>
                    <li>
                        Svatba bude v letnm obdob u rybnku. Svatbu jsme tam zatm jet nemli, tak Vm
                        nedokeme ct, jak podmnky
                        tam pro ns budou nastraen, proto prosm myslete na to, abyste si s sebou vzali pro
                        jistotu repelent.
                    </li>
                    <li>
                        Nkdy v prbhu veera, bychom si pli mt jako vsuvku hran na kytary. Proto, pokud njakou
                        mte a hrajete rdi,
                        si ji mete vzt s sebou, urit se neztrat.
                    </li>
                    <li>Jste-li milovnky njak konkrtn psniky, napit nm a zaadme ji mezi
                        n svatebn playlist.
                    </li>
                    <li>
                        Vechny hosty, kte kou, prosme, abyste byli k ostatnm ohledupln a pedem si
                        ovili,
                        na jakm mst bude kouen povoleno.
                    </li>
                    <li>
                        Jestli mte v plnu vymyslet njakou zbavu pro enicha s nevstou, dejte o tom vdt svdkm,
                        aby se na to myslelo v harmonogramu a zbyl na to as.
                    </li>
                </ul>

            </p>
            <div className={styles.horizontal_lines}>
                <div onClick={() => {
                    setShowAddress(!showAddress);
                    setShowSchedule(false);
                    setShowPressents(false);
                    setShowDresscode(false);
                }
                }
                >Adresa
                </div>
                <div onClick={() => {
                    setShowAddress(false);
                    setShowSchedule(!showSchedule);
                    setShowPressents(false);
                    setShowDresscode(false);
                }
                }
                >Program
                </div>
                <div onClick={() => {
                    setShowAddress(false);
                    setShowSchedule(false);
                    setShowPressents(!showPressents);
                    setShowDresscode(false);
                }

                }
                >Drky
                </div>
                <div onClick={() => {
                    setShowAddress(false);
                    setShowSchedule(false);
                    setShowPressents(false);
                    setShowDresscode(!showDresscode);
                }
                }
                >Dresscode
                </div>
            </div>
            <div id={styles.information_content}>
                {showAddress ? <Address/> : ""}
                {showSchedule ? <Schedule/> : ""}
                {showPressents ? <Pressents/> : ""}
                {showDresscode ? <Dresscode/> : ""}
            </div>
        </div>
    )
}