import styles from './style.module.scss'
import collage from './collage1.png'
import mainImage from './mainImage.jpg'
import {useState} from "react";
import InvitationModal from "../InvitationModal/InvitationModal";

export const WelcomePart = () => {

    const [openInvitationModal, setOpenInvitationModal] = useState(false);
    const [scrollY, setScrollY] = useState(0);


    const openModal = () => {
        setOpenInvitationModal(true);
    };

    const closeModal = () => {
        setOpenInvitationModal(false);
    }
    return (
        <>
            <div id={styles.welcome_part}>
                <div className={styles.horizontal_line}>
                    <img id={styles.web_collage}
                         src={collage} alt="Our image"
                         width="0"
                         height="0"/>
                    <img id={styles.mobile_image}
                         src={mainImage} alt="Our image"
                         width="0"
                         height="0"/>

                    <div className={styles.center_content}>
                        <h2 className={styles.section_title}>
                            Vtejte na naich svatebnch strnkch!
                        </h2>
                        <div>
                            Ven host,
                            <p>
                                srden Vs vtme na naich svatebnch strnkch, kter Vm sdl vechny dleit informace
                                o na svatb. Na n velk den se extrmn tme a jsme rdi,
                                e se s Vmi meme podlit o tuto vjimenou udlost naich ivot.
                            </p>
                            <p>
                                Snaili jsme se, aby tyto strnky byly uitenm zdrojem informac,
                                proto Vs dme, abyste si je proli cel, abyste se dozvdli ve, co jsme si pro Vs
                                pipravili.
                                Budete-li mt jakkoliv otzky, nevhejte se na ns obrtit.
                            </p>
                            <p>
                                Doufme, e si prohlen tchto strnek a cel n velk den uijete
                                a my se tme na nae spolen setkn.
                            </p>
                            <p>
                                S lskou
                            </p>
                            <p> Sonika a Ondra
                            </p>
                        </div>

                        <div className={styles.button_right}>
                            <input id={styles.welcome_button} type="button" value=""
                                   onClick={() => openModal()}/>
                        </div>
                    </div>
                </div>
            </div>
            <div style={{position: 'fixed', top: scrollY}} id={styles.modal}>
                {openInvitationModal &&
                    <InvitationModal closeInvitationModal={closeModal}/>
                }
            </div>
        </>
    )
}