import styles from './style.module.scss'
import collage from './collage2.png'
import engagement from './engagement.jpg'
import childhood from './childhood.png'
import {useEffect, useState} from "react";
import InvitationModal from "../InvitationModal/InvitationModal";

export const OurStoryPart = () => {

    const [openInvitationModal, setOpenInvitationModal] = useState(false);
    const [scrollY, setScrollY] = useState(0);

    const openModal = () => {
        setOpenInvitationModal(true);
    };

    const closeModal = () => {
        setOpenInvitationModal(false);
    }

    return (
        <div className={styles.our_story} id={'our_story'}>
            <h2 className={styles.center_content} id={styles.our_section_title}>
                Jak je n pbh?
            </h2>
            <div className={styles.horizontal_lines} id={styles.white_part}>
                <div className={styles.white_part_content}>
                    <p>
                        Kdysi dvno, legendy prav zhruba kolem roku 2005, v zemi nm vem dobe znm,
                        ve velkomst pevelikm zvanm Neratovice, existovala kolka.
                        Lid tohoto velkomsta ji nazval kolkou Psnikou.
                        A do tto kolky chodili dva mal punti. On byl mal tramk, rozen Cassanova
                        a ona byla tou nejroztomilej princeznikou, kter se tam vyskytovala.
                        Hodili by se k sob, vak mete to posoudit sami.
                        Nicmn jeden romantick veer na kolce v prod bylo to jedin, co jim tehdy bylo pno.
                        Osud je vak vrtkav, a tak cesty tchto malch punt na dlouh roky rozdlil.
                    </p>
                    <p>
                        Tak ten pbh ovem neskonil, ptte-li se. Naopak v roce 2015 teprve cel zapoal.
                        Jednoho dne se ji o nco mn mal Ondra objevil shodou okolnost ped neptelskou kolou.
                        Toho dne se znovu potkali. On nebyl si jist, e ta slin mlad dma je ta, kterou ve kolce ztratil.
                        Jeho podezen ovem bylo siln... A pro oba dva se ukzalo, e i pravdiv.
                    </p>
                </div>
                <div id={styles.childhood_image_div}>
                    <img src={childhood} alt="Our image"
                         height={"0"} width={"0"}/>
                </div>

            </div>
            <div className={styles.horizontal_lines} id={styles.yellow_part}>

                <img src={collage} alt="Our image"
                     width={"0"} height={"0"}/>
                <div>
                    <p>
                        A tak se stalo, e se mlad rebel, kter il jen bhnm a prknem a roztomil aerobika,
                        kter eila jen kolu a trnovn, znovu potkali.
                        Bavili se spolu, hudbu poslouchali, na koncerty chodili, mnoho kokos si spolu o obrubnk rozbili a stle bl si byli.
                        Stali se skvlmi pteli, ale bylo v tom nco vc...
                    </p>
                    <p>
                        To vc se datuje od 20.6.2016. Byly to krsn roky. A pokud o nich chcete vdt vce,
                        tak pijete 26. 8. 2023 na jejich svatbu a zeptejte se tohoto pru sami, jak to cel vlastn bylo.
                        U Vm meme ale prozradit, e ty 2 dti z prvnho obrzku jsme my.

                    </p>
                </div>
            </div>
            <div className={styles.horizontal_lines} id={styles.black_part}>
                <div className={styles.invitation_part}>
                    <h2 className={styles.section_title}>A tak jsme se rozhodli se po 7 letech vzt.</h2>
                    <div>
                        <input type="button" value="Pozvnka"
                               onClick={() => openModal()}/>
                    </div>
                </div>
                <img src={engagement} alt="Our image"
                     width={"0"} height={"0"}/>
            </div>
            {/* TODO */}
            <div style={{ position: 'fixed', top: scrollY}} id={styles.modal}>
                {  openInvitationModal &&
                    <InvitationModal closeInvitationModal={closeModal}/>
                }
            </div>
        </div>
    )
}