import styles from './style.module.scss'

export const Pressents = () => {

    return (
        <div className={styles.center_content}>
            <p>
                Dkujeme Vm za V zjem o nai svatbu a za Vai pze.
                Mli bychom na Vs ovem prosbu. Pokud si pejete nm pedat njak svatebn dar
                do naeho novho ivotnho dobrodrustv, tak bychom Vs podali, aby byl ve finann podob,
                tyto finance bychom pouili pro na spolenou budoucnost podle toho, kde zrovna bude poteba.
            </p>
            <p>
                Pokud byste si ale i pesto pli nm darovat nco nefinannho,
                prosme Vs, abyste se s nmi nebo naimi svdky pedem spojili.
                Rdi s Vmi toto tma probereme. :)
            </p>
        </div>
    )
}